//rsc
import "bootstrap/dist/css/bootstrap.min.css";
import React, { useState } from "react";
import { useMediaQuery } from "react-responsive";
import axios from "axios";
import { useNavigate } from "react-router-dom";

const VisitForm = () => {
  const isPc = useMediaQuery({
    query: "(min-width:1024px)",
  });

  const isTablet = useMediaQuery({
    query: "(min-width:768px) and (max-width:1023px)",
  });

  const isMobile = useMediaQuery({
    query: "(max-width:767px)",
  });

  let screen_style = { margin: "auto", minWidth: "600px", width: "600px" };

  if (isPc) {
    screen_style = { margin: "auto", minWidth: "600px", width: "600px" };
  } else if (isTablet) {
    screen_style = { margin: "auto", minWidth: "600px", width: "90%" };
  } else if (isMobile) {
    screen_style = { margin: "auto", minWidth: "400px", width: "90%" };
  }

  // 컴포넌트 안에서 쓸 상태변수 선언해야 함
  // 변수를 json으로 감싸서 보내므로 data를 json으로 만들거다
  const [visit, setVisit] = useState({ name: "", content: "", pwd: "" });

  //화면 전환 시키기 위한 객체 만들기 => useNavigate import
  const navigator = useNavigate();

  // data가 업데이트 되었을 때 사용하는 함수
  function updateVisit(e) {
    let id = e.target.id;
    let value = e.target.value;
    setVisit({ ...visit, [id]: value });
  }

  function addVisit() {
    // 입력값 체크
    if (visit.name === "") {
      alert("이름을 입력해주세요");
      return;
    }
    if (visit.content === "") {
      alert("내용을 입력해주세요");
      return;
    }
    if (visit.pwd === "") {
      alert("비밀번호를 입력해주세요");
      return;
    }

    // POST 요청(insert) : parameter는 JSON형식
    const url = "http://localhost:8080/rest/visit";
    axios
      .post(url, visit)
      .then(function (response) {
        console.log(response.data);
        if (response.data.result == false) {
          alert("등록에 실패하였습니다.");
          return;
        }
        //화면초기화(필요는 없다)
        //setVisit({name:"",content:"",pwd:""})

        // 목록보기 화면으로 이동
        navigator("/");
      })
      .catch(function (error) {
        console.log(error);
      });
  }

  // 32번째 줄 screen_style 대신 상단의  isPc,isTablet,isMobile 넣으면 각각의 스타일 적용됨
  return (
    <div>
      <div style={screen_style}>
        <form className="form-inline">
          <h2 style={{ color: "#999", fontSize: "20px" }}>
            ♣ 방명록 작성하기 ♣
          </h2>
          <br />

          <input
            className="form-control"
            id="name"
            value={visit.name}
            placeholder="작성자명을 입력하세요"
            onChange={updateVisit}
          />
          <br />

          <textarea
            className="form-control"
            style={{ width: "100%", resize: "none" }}
            id="content"
            value={visit.content}
            rows={5}
            cols={50}
            placeholder="내용을 입력하세요"
            onChange={updateVisit}
          ></textarea>
          <br />

          <input
            className="form-control"
            type="password"
            id="pwd"
            value={visit.pwd}
            placeholder="비밀번호 입력하세요"
            onChange={updateVisit}
          />
          <br />

          <input
            className="btn btn-success"
            type="button"
            value="글올리기"
            onClick={addVisit} // addVisit() 함수 만들기
          />
        </form>
      </div>
    </div>
  );
};

export default VisitForm;
