// rsc
import DeleteIcon from "@mui/icons-material/Delete";
import { IconButton } from "@mui/material";
import axios from "axios";
import "bootstrap/dist/css/bootstrap.min.css";
import React, { useEffect, useRef, useState } from "react";
import { useMediaQuery } from "react-responsive";
import InfiniteScroll from "react-infinite-scroller";

const VisitList = () => {
  // MediaQuery 사용 -> 반응형 적용
  const isPc = useMediaQuery({
    query: "(min-width:1024px)",
  });

  const isTablet = useMediaQuery({
    query: "(min-width:768px) and (max-width:1023px)",
  });

  const isMobile = useMediaQuery({
    query: "(max-width:767px)",
  });

  let screen_style = { margin: "auto", minWidth: "600px", width: "600px" };

  if (isPc) {
    screen_style = { margin: "auto", minWidth: "600px", width: "600px" };
  } else if (isTablet) {
    screen_style = { margin: "auto", minWidth: "600px", width: "90%" };
  } else if (isMobile) {
    screen_style = { margin: "auto", minWidth: "400px", width: "90%" };
  }

  //최초사용전 CORS인증 -> 지금은 local에서만 접근하므로 안 써도 되지만 외부 접근? 있을 경우 사용해줘야 함. 인증서 같은 느낌. 신뢰할거냐
  //axios.defaults.withCredentials = true;

  // 내부상태관리하는변수 :  ReRendering이 된다
  // useState -> 값이(data) 변동되면 변경된 내용을 바로 반영해서 보여주기 위해 사용
  const [list, setList] = useState([]);

  // 더보기 버튼 관련 변수 => useRef import하기
  const total_count = useRef(0);  // 전체 데이터 갯수
  const count = useRef(3);  // 현재화면에 보여진 데이터 갯수
  const isLast = useRef(false);  // 데이터를 모두 보여줬냐? 

  // 더보기 버튼 관련 함수
  const handleLoadMore = () => {
    //console.log("---handleLoadMore()---");
    count.current = count.current + 3;

    if (count.current >= total_count.current) { // 
      isLast.current = true;// setLast(true); // true로 바꿔주자.
    } else {
      isLast.current = false;//setLast(false);
    }
    getData();

  };

  const scrollToTop = () => {
    window.scrollTo(0, 0);
  };


  useEffect(() => {
    // Data 읽어오는 코드
    // console.log("--useEffect()--");
    getData();
  }, []); // 빈 브라켓 쓰는 이유 -> 안 쓰면 무한대로 계속 데이터 받아옴

  function getData() {
    // server 쪽 데이터 있는 url..?
    const url = "http://localhost:8080/rest/visits/" + count.current;

    // GET 요청
    axios
      .get(url)
      .then(function (response) {
        console.log(response.data);

        //useState()정보가 갱신-> ReRendering
        setList(response.data.list);

        // 전체 데이터 갯수 받기-> 이 데이터를 받아와야 총 데이터가 얼마나 있는지 알 수 있음
        total_count.current = response.data.total_count;

      })
      .catch(function (error) {
        console.log(error);
      });
  }

  // 삭제 함수 만들기
  function deleteVisit(idx) {
    const c_pwd = prompt("삭제 비밀번호:");

    //취소하기
    if (c_pwd == null) return;

    // 입력되지 않은 경우
    if (c_pwd === "") {
      alert("삭제를 위해 비밀번호를 입력해주세요");
      return;
    }

    // GET 요청(select) : 비밀번호 확인 요청
    const url = `http://localhost:8080/rest/check-pwd/${idx}/${c_pwd}`;
    axios
      .get(url)
      .then(function (response) {
        console.log(response);
        // 응답 내용 = true or false
        if (response.data.result === false) {
          alert("삭제 게시물의 비밀번호가 틀렸습니다.");
          return;
        }

        // 삭제 확인
        if (window.confirm("정말 삭제하시겠습니까?") === false) return;

        // 삭제 처리 함수로 이동
        deleteVisitData(idx);
      })
      .catch(function (error) {
        console.log(error);
      });
  } // end : deleteVisit()

  // 삭제 처리 함수 -> idx 가지고 삭제함
  function deleteVisitData(idx) {
    const url = "http://localhost:8080/rest/visit/" + idx;
    // DELETE 요청(delete)
    axios
      .delete(url)
      .then(function (response) {
        console.log(response);
        if (response.data.result == false) {
          alert("삭제 실패");
          return;
        }

        // 삭제된 데이터가 반영된 새로운 데이터 요청
        getData();
      })

      .catch(function (error) {
        console.log(error);
      });
  } // end: deleteVisitData();

  return (

    <InfiniteScroll
      className="h-[85%] smartPhone:h-[70%]  scrollbar-hide"
      pageStart={0} // 가져올 데이터 수
      loadMore={getData}
    >

      <main>
        {/* <div className="flex flex-col items-center justify-center h-full"> */}

        <div className="flex flex-col h-full" style={screen_style}>
          {list &&
            list.map((v, index) => (
              <div
                key={index}
                style={{
                  border: "1px solid black",
                  margin: "10px",
                  padding: "20px",
                  boxShadow: "1px 1px 3px black",
                }}
              >
                {/* 첫번째줄 */}
                <div className="flex flex-row">
                  <span
                    style={{
                      display: "inline-block",
                      width: "80%",
                      textAlign: "left",
                      color: "black",
                      fontWeight: "bold",
                    }}
                  >
                    ♥ {v.name}({v.pwd})
                  </span>
                  <span
                    style={{
                      display: "inline-block",
                      width: "20%",
                      textAlign: "right",
                    }}
                  >
                    <IconButton
                      aria-label="delete"
                      id={v.idx}
                      onClick={() => deleteVisit(v.idx)}
                    >
                      <DeleteIcon />
                    </IconButton>
                  </span>
                </div>
                {/* 두번째줄 */}
                <p
                  style={{
                    textAlign: "left",
                    color: "gray",
                    whiteSpace: "pre-wrap",
                  }}
                >
                  {v.content.replaceAll("<br>", "\n")}
                </p>

                {/* 세번째줄 */}
                <p style={{ textAlign: "left", color: "#ccc" }}>{v.regdate}</p>
              </div>
            ))}

          {/* 더보기 버튼 출력 */}
          {!isLast.current && (
            <button
              onClick={handleLoadMore}
              className="btn btn-success mybtn"
            >
              더보기...
            </button>
          )}


          {isLast.current && (
            <button
              onClick={scrollToTop}
              className="btn btn-success mybtn"
            >
              맨위로...
            </button>
          )}
        </div>
      </main>
    </InfiniteScroll>
  );
};

export default VisitList;
